.search__input{
    display:flex;
    align-items: center;
    height:30px;
    padding:10px 20px;
    border-radius: 999px;
    border:1px solid gray;
    /* width:75vw; */
    margin:0 auto;
    margin-top:15px;
    max-width: 560px;
}

.search__input input{
    flex:1;
    padding:10px 20px;
    font-size: medium;
    border:none;
}
.search__input input:focus{
  outline-width:0;
}
.search__inputIcon{
  color: gray;
}


.search__buttons{
    display: flex;
    justify-content: center;
    margin-top: 30px;
}
.search__buttons button{
  margin:5px;
  padding:7px 15px;
  background-color: #f8f8f8;
  border:1px solid white;
  text-transform: inherit; /*Used to make it the way we write otherwise button text are in Capital letter by default */
  color:#5f6368;
}
.search__buttons button:hover{
 box-shadow: 0 1px 1px rgba(0,0,0,0.1);
 background-color: #f8f8f8;
 border:1px solid #c6c6c6;
 color:#222;
}

.search__buttonsHidden{
    display: none !important;
}