.home{
    display:flex;
    flex-direction: column;
    height:100vh;
}

.home__header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding:20px 30px;
}

/* Actually even to style <Link> we use <a> and style like anchor tag  */
.home__header a{
   margin-right:20px;
   text-decoration: none;
   color:rgba(0,0,0,0.87);  /*Color picked from google source code so as to keep it similar*/
   font-size:15px;
}
.home__header a:hover{
  text-decoration: underline;
  cursor: pointer;
}

.home__headerRight{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width:13vw;
}
.home__headerRight >.MuiSvgIcon-root{
    margin-right:20px; /* To give space between app icon and avatar and .MuiSvgIcon-root is the default class provided by material Ui to its icons  */
                                                                 
}
.home__body{
    flex: 1;/*It means to say that home__body should take majority of the space of body after home__header*/
    display:flex;
    flex-direction:column; 
    margin-top: 10%;
}
.home__body img{
    /*This outject-fit will allow the image in the given dimension and image keeps its aspect ratio*/
    object-fit: contain;
    height:150px;
    margin-top:-32px;
   
}
